	module.exports = {"data":{"wordpressPost":{"title":"#BMJOpen5years: 2014 article highlight","content":"<p><em>This week we’re celebrating our fifth anniversary by highlighting some of the articles that have gained us attention, along with some that demonstrate our unique approach. </em></p>\n<p>Cited 49 times in Web of Science, and with an Altmetric score of 306, our 2014 article highlight investigates the relationship between <a href=\"http://bmjopen.bmj.com/content/4/4/e004549.full\">salt intake, blood pressure, stroke, and ischaemic heart disease mortality</a>.</p>\n<p>Using data from more than 31,500 people, Feng He and colleagues show that salt intake decreased by 15% in England from 2003-2011. During the same period, heart disease and stroke deaths fell by around 40%.</p>\n<p>While several factors are likely at play, the results are encouraging. However, with 70% of the English population still consuming more than the recommended quantity of salt (6 g/day)&#8211;much of it coming from processed foods&#8211;further efforts to reduce salt consumption are needed.</p>\n<p><a href=\"https://blogs.bmj.com/bmjopen/files/2016/02/Salt.png\" rel=\"attachment wp-att-966\"><img class=\"alignnone  wp-image-966\" src=\"https://blogs.bmj.com/bmjopen/files/2016/02/Salt-300x260.png\" alt=\"Salt\" width=\"313\" height=\"271\" srcset=\"https://blogs.bmj.com/bmjopen/files/2016/02/Salt-300x260.png 300w, https://blogs.bmj.com/bmjopen/files/2016/02/Salt.png 513w\" sizes=\"(max-width: 313px) 100vw, 313px\" /></a><!--TrendMD v2.4.6--></p>\n"},"site":{"siteMetadata":{"title":"Gatsby Default Starter"}}},"pathContext":{"id":"cc054e45-c20f-5922-b273-4e2805b1652a"}}

